import { I as ISStages } from "../../../chunks/stage_name_lookup_table.js";
const LANGUAGES = ["en", "ja", "zh"];
const PAGES = ["about", "donate", "laoli", "recruit", "mizuki", "phantom", "sarkaz", "sami"];
const IS_STAGES_LIST = Object.entries(ISStages).reduce((acc, [name, value]) => {
  const { lang } = value;
  if (lang === "ALL") {
    const list = LANGUAGES.map((language) => `/${language}/stages/${name}`);
    acc = acc.concat(list);
  } else {
    acc.push(`/${lang}/stages/${name}`);
  }
  return acc;
}, []);
const pagesToRender = LANGUAGES.reduce((acc, language) => {
  for (const page of PAGES) {
    acc.push(`/${language}/${page}`);
  }
  return acc;
}, []).concat(IS_STAGES_LIST);
async function GET() {
  const mainUrl = "https://tomimi.dev";
  return new Response(
    `
        <?xml version="1.0" encoding="UTF-8" ?>
        <urlset
            xmlns="https://www.sitemaps.org/schemas/sitemap/0.9"
            xmlns:xhtml="https://www.w3.org/1999/xhtml"
            xmlns:mobile="https://www.google.com/schemas/sitemap-mobile/1.0"
            xmlns:news="https://www.google.com/schemas/sitemap-news/0.9"
            xmlns:image="https://www.google.com/schemas/sitemap-image/1.1"
            xmlns:video="https://www.google.com/schemas/sitemap-video/1.1"
        >
            ${pagesToRender.map(
      (url) => `<url>
									<loc>${mainUrl}${url}</loc>
									<changefreq>Monthly</changefreq>
								</url>`
    ).join("\n")}
        </urlset>`.trim(),
    {
      headers: {
        "Content-Type": "application/xml"
      }
    }
  );
}
export {
  GET
};
